* {
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

h1 {
    font-size: 55px;
}

a {

    text-decoration: none;
    text-transform: uppercase;

    &:hover{
        text-decoration: $blue;
    }
}

body {
    margin: 0;
    background-color: #eaeaea;
    font-size: 1.14vmax;
    line-height: 1.33vmax;
}

header {}

.container-fluid {
    margin: 0;
    padding: 0;
}

.wrapper {
    padding-left: 10%;
    padding-right: 10%;
}