.header-top {
    display: flex;
    justify-content: space-between;
    padding: 30px 15%;
    color: #fff;
    background-color: #2e2e30;
    height: 110px;
}

.header-top__top-btn {
    height: 35px;
    background-color: #2e2e30;
    padding-left: 15px;
    padding-right: 15px;
    text-transform: uppercase;
    border-radius: 10px;
    letter-spacing: 2px;
    font-size: 12px;
}

.header-top__btn-signin {
    border: 1px solid #7d7d7d;
    color: #fff;
}

.header-top__btn-cart {
    border: 1px solid #89d591;
    color: #89d591;
}
